.event-noti {
    position: relative;
    background-color: #111;  
    z-index: 25; 
    height: 40px;
    width: 100%;

    &__link {
        position: absolute;
        display: block;
        width: 100%;
        height: 100%;
        z-index: 2;
    }

    &__container {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        padding: 0 15px;

        @media (min-width: 768px) {
            max-width: 980px;
        }

        @media (min-width: 1280px) {
            max-width: 1140px;
        }
    }

    &__brand {
        display: flex;
        position: absolute;
        top: 0;
        height: 40px;

        &__img {
            display: inline-block;
            vertical-align: middle;
        }

        img {
            max-width: 100%;
            max-height: 100%;
        }
    }

    &__message {
        font-size: .875rem;
        color: #fff;
        width: 100%;
        height: 20px;
        overflow: hidden;
        margin-top: 5px;
        margin-bottom: 5px;
        margin-left: 55px;
    }

    &__status {     
        width: 80px;   
        margin-left: 15px;

        &__btn {
            background-color: #f7412d;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: .875rem;
            color: #fff;
            font-weight: bold;
            text-transform: uppercase;
            border-radius: 5px;
            transition-duration: 0.2s;
            padding: 2px 10px;

            .dot {
                background-color: #fff;
                display: inline-block;
                width: 8px;
                height: 8px;
                border-radius: 50%;
                animation: pulse 1s infinite;
                margin-right: 5px;
            }

            &:hover {
                background-color: #fff;
                color: #f7412d;
                text-decoration: none;

                .dot {
                    background-color: #f7412d;
                }
            }
        }
    }
}

@keyframes pulse {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}